.wrapper {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
}

.image {
    flex: 0 0 50%;
    position: relative;
    padding-bottom: 55.5%;
    width: 100%;
}

.image img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.text {
    flex: 0 0 50%;
    background-color: #996B60;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    padding-left: 4.2%;
    padding-right: 4.2%;
    padding-bottom: 100px;

}
.text >*:not(:last-child){
    margin-bottom: 34px;
}
.text_title {
    font-family: 'Libre Caslon Text', serif;
    font-size: 40px;
    text-transform: uppercase;
    font-weight: 400;
    line-height: 56px;
    letter-spacing: 0em;
    text-align: left;
    margin: 0;
    color: #FFFFFF;
}

.text_subtitle {
    font-family: 'Roboto', sans-serif;
    font-size: 18px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF;
    max-width: 572px;
}

@media(max-width: 998px) {
    .wrapper {
        flex-wrap: wrap;
    }
    .image {
        flex: 1 1 100%;

    }
    .text {
        flex: 1 1 100%;
        padding: 50px 15px;
    }
    .text_subtitle {
        max-width: 100%;
    }

}