.cartWrap {
    display: flex;
    flex-direction: column;
    margin-top: 25px;
    margin-bottom: 25px;
}
.cartWrap > *:not(:last-child) {
    margin-bottom: 25px;
}
.cart_item {
    display: flex;
    padding: 15px;
    border: 1px solid #000;
    border-radius: 4px;
}
.container {

}
.cart_img {
    position: relative;
    padding-bottom: 25%;
    width: 100%;
    max-width: 25%;
    margin-right: 25px;
}
.cart_img img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: contain;
}
.cart_info {
    max-width: calc(75% - 25px);
    display: flex;
    flex-direction: column;
}
.cart_info > *:not(:last-child) {
    margin-bottom: 25px;
}

.name {
    margin: 0 auto 16px auto;
    font-family: 'Libre Caslon Text', serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 44px;
    letter-spacing: 0em;
    text-align: left;
    color: #000;
}
.price {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    font-weight: 400;
    line-height: 44px;
    letter-spacing: 0em;
    text-align: left;
    color: #000;
}

.cart_quntity {

    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 120px;
    border: 1px solid #000;
    border-radius: 5px;
}

.btn_minus {
    width: 33%;
    border: none;
    outline: none;
    background-color: #ccc;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    cursor: pointer;
}

.btn_plus {
    width: 33%;
    border: none;
    outline: none;
    background-color: #ccc;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    cursor: pointer;
}

.cartHow {
    text-align: right;
    width: 33%;
    border: none;
    outline: none;
    pointer-events: none;
    background-color: transparent;
}
.totalPrice {
    
}
.cartIsEmpty {
    margin: 0 auto 16px auto;
    font-family: 'Libre Caslon Text', serif;
    font-size: 30px;
    font-weight: 400;
    line-height: 44px;
    letter-spacing: 0em;
    text-align: center;
    color: #000;
}

.cartLink  {
    text-transform: uppercase;
    font-family: "Roboto", sans-serif;
    font-size: 12px;
    font-weight: 400;
    line-height: 18px;
    letter-spacing: 1px;
    text-align: center;
    color: #000;
    position: relative;
    display: flex;
    justify-content: center;
    margin: 0 auto;
    width: fit-content
}

.cartLink::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #000;
    transform: scale(0);
    transition: all 0.3s;
}
.cartLink:hover::before {
    transform: scale(1);
    transition: all 0.3s;
}
.removeBtn {
    cursor: pointer;
    padding: 5px;
    border: 1px solid #000;
    border-radius: 5px;
    width: fit-content;
    transition: all 0.3s;
}
.removeBtn:hover {
    background-color: #000;
    color: #fff;
    transition: all 0.3s;
}
@media (max-width: 768px) {
    .cart_item {
        display: flex;
        flex-direction: column;
    }
    .cart_info {
        max-width: 100%;
    }
    .cart_img {
        margin: 0 0 25px 0;
        width: 100%;
        max-width: 100%;
        padding-bottom: 100%;
    }
}